@media screen and (max-width: 767px){
    .main-nav{
        display: none;
    }
}
@media screen and (min-width: 768px){
    .main-nav{
        height: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 0;
        padding: 0;
        list-style-type: none;
    }
    .main-nav li a{
        text-decoration: none;
        color: #B1AFFF;
        cursor: pointer;
        transition: color .4s;
    }
    .main-nav li a:hover{
        color: #7b77ff;
    }
}
