.wrapper{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
    margin: 60px;
}
.contact-title{
    margin-bottom: 20px;
    color: #ff91d9;
    font-weight: bold;
}
.grid{
    display: grid;
    grid-template-areas: 
    "one two"
    "one three";
    gap: 20px;
}
img{
    width: 100%;
}
.img1{
    grid-area: one;
}
.img2{
    grid-area: two;
}
.img3{
    grid-area: three;
}
.icon{
    width: 45px;
    height: 45px;
    border: 2px solid #ff91d9;
    border-radius: 50%;
    text-align: center;
    line-height: 45px;
}
.icon i{
    color: #ff91d9;
    font-size: 24px;
}
.icon-wrapper{
    display: flex;
    gap: 30px;
    align-items: center;
    margin: 40px 0;
}
.icon-wrapper div{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 20px;
}
.icon-wrapper p{
    margin-bottom: 0;
}