.shoppingbagIcon{
    cursor: pointer;
    font-size: 40px;
    margin-left: 1rem;
    margin-right: 1rem;
}
.shoppingbagIcon:hover{
    color: #9490ff;
}
.count{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: #8783ff;
    position: absolute;
    text-align: center;
    color: #fff;
}
a:-webkit-any-link{
    color: #9e9bff;
}
