@font-face{
  font-family: vazir;
  /* src: url(../public/fonts/Vazir.woff) format('woff'); */
  src: url(../public/fonts/Vazir.ttf) format('truetype');
  /* src: url(../public/fonts/Vazir.eot); */
}
.dark-theme {
  --clr-bcg: #1b1d24;
  --clr-font: #fff;
}
.light-theme {
  --clr-bcg: #fff;
  --clr-font: #282c35;
}
body {
  margin: 0;
  font-family: vazir, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  direction: rtl;
  background-color: var(--clr-bcg);
}
button {
  font-family: vazir, sans-serif;
}
.title , .brand h4 , .allBtn , .noItem , .best-of-today{
  color: var(--clr-font);
}
.themeIcon{
  color: var(--clr-font);
}
.card{
  background-color: var(--clr-bcg);
}
