.application-download{
    border: 2px solid #ff95da;
    color: #ff95da;
    background-color: transparent;
    outline: 0;
    padding: 12px 44px;
    margin-top: 20px;
    border-radius: 30px;
}
.sns img{
    width: 30px;
    height: 30px;
}
.sns{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    margin: 35px 0;
}
.sns div{
    width: 50px;
    height: 50px;
    border: 2px solid var(--clr-font);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: transform .3s ease-in-out;
}
.sns div:hover{
    transform: scale(1.2);
}
.footer-ul{
    list-style-type: none;
    padding-right: 5px;
}
.footer-ul li{
    margin: 15px 0;
    cursor: pointer;
}
.copyright{
    font-size: 13px;
}
.neshan{
    display: flex;
    justify-content: space-between;
    align-items: center;
}