.dark-theme {
    --clr-bcg: #1b1d24;
    --clr-font: #fff;
  }
  .light-theme {
    --clr-bcg: #fff;
    --clr-font: #282c35;
  }
  body{
    background-color: var(--clr-bcg);
    color: var(--clr-font);
  }
@media all and (max-width: 767px){
    .hero-content h1{
        font-size: 2rem;
    }
    .hero-content p{
        font-size: 1rem;
    }
    .hero-content button a{
        color: #fff;
        text-decoration: none;
    }
    .email{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 10px;
    }
}
@media all and (min-width: 768px){
    .hero-content h1{
        font-size: 4.3rem;
        margin-bottom: 10px;
    }
    .hero-content p{
        font-size: 1.3rem;
    }
    .hero-content button a{
        color: #fff;
        text-decoration: none;
        font-size: 1.2rem;
    }
}
video{
    width: 100%;
}
.hero-content{
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(124, 119, 255, 0.521);
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: #fff;
}
.hero-content button{
    width: 200px;
    height: 50px;
    border-radius: 40px;
    margin-top: 10px;
    border: 0;
    outline: 0;
    background-color: #ff95da;
    transition: all .5s ease-in-out;
}
.hero-content button:hover{
    background-color: #fff;
}
.hero-content button:hover a{
    color: #ff95da;
}
.category-img{
    transform: scale(0.75);
    cursor: pointer;
    transition: transform 0.5s;
    border-radius: 20px;
}
.category-img:hover{
    transform: scale(0.9);
}
.img-bg{
    transform: scale(0.7);
    border-radius: 50%;
    background-color: #b2afff;
}
.section4-p{
    text-align: center;
    font-size: 0.9rem;
}
.best-of-today{
    margin-bottom: 3.5rem;
    font-size: 1.7rem;
    text-align: center;
}
.best-of-today i{
    font-size: 2rem;
    margin-left: 12px;
}
.bg-shadow{
    background-color: #fff;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    padding: 1rem;
}
.show-all-btn{
    width: 180px;
    height: 55px;
    border-radius: 40px;
    margin-top: 10px;
    border: 0;
    outline: 0;
    background-color: #000;
    transition: all .5s ease-in-out;
    border: 2px solid #000;
}
.show-all-btn a{
    text-decoration: none;
    color: #fff;
}
.show-all-btn:hover{
    background-color: #fff;
    border: 2px solid #000;
}
.show-all-btn:hover a{
    color: #000;
}
.bg-purple{
    background-color: #bbb9ff;
}
.email input{
    background-color: #fff;
    border-radius: 20px;
    border: 0;
    outline: 0;
    margin-left: 20px;
    padding: 8px 38px;
}
.email button{
    border-radius: 20px;
    background-color: #8682ff;
    border: 0;
    outline: 0;
    padding: 8px 30px;
    color: #fff;
}