.content-wrapper{
    display: flex;
    flex-direction: column;
    gap: 30px;
}
.content-wrapper h2{
    color: #b183ff;
}
.content-wrapper input{
    outline: 0;
    border: 2px solid #b991ff;
    border-radius: 30px;
    padding: 10px 20px;
    color: #b991ff;
    transition: all .5s ease-in;
}
.content-wrapper input:focus{
    border: 3px solid #b183ff;
}
.register-btn{
    background-color: #b183ff;
    color: #fff;
    border: 0;
    outline: 0;
    border-radius: 30px;
    padding: 10px 0;
    transition: all .4s ease-in-out;
}
.register-btn:hover{
    background-color: #ff95da;
    color: #fff;
}