.amount{
    display: flex;
    align-items: center;
    margin-left: 150px;
}
.amount button{
    width: 40px;
    height: 40px;
    cursor: pointer;
    border: 0;
    outline: 0;
    border-radius: 5px;
    font-size: 18px;
    padding: 5px;
    background-color: #e6e6ff;
}
.amount :hover{
    background-color: #cdcdff;
}
.amount p{
    margin: 0 20px;
}
.finalPrice{
    color: #7b77ff;
    font-weight: bold;
}