.wrapper{
    padding: 30px;
}
.title{
    display: flex;
    justify-content: space-between;
    color: rgb(135, 135, 135);
    font-weight: 600;
    margin-bottom: 30px;
}
.title p:nth-child(1){
    margin-left: 400px;
}
.title p:last-child{
    margin-left: 100px;
}
.card{
    background-color: #fff;
    color: #000;
    position: relative;
    display: flex;
    padding: 20px;
    align-items: center;
    margin-bottom: 30px;
    border-radius: 20px;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
}
.card img{
    width: 200px;
    height: 200px;
    margin-left: 30px;
}
.amount{
    display: flex;
    align-items: center;
    margin-left: 150px;
}
.amount button{
    width: 40px;
    height: 40px;
    cursor: pointer;
    border: 0;
    outline: 0;
    border-radius: 5px;
    font-size: 18px;
    padding: 5px;
    background-color: #e6e6ff;
}
.amount :hover{
    background-color: #cdcdff;
}
.amount p{
    margin: 0 20px;
}
.desc{
    margin-left: 120px;
}
.price{
    margin-left: 180px;
}
.deleteBtn{
    border-radius: 50%;
    border: 0;
    outline: 0;
    color: #fff;
    background-color: rgb(138, 138, 138);
    width: 35px;
    height: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    position: absolute;
    top: -10px;
    right: -10px;
}
.deleteBtn:hover{
    background-color: rgb(220, 45, 45);
}
.empty{
    background-color: #d4d2ff;
    border-radius: 7px;
    padding: 50px;
    text-align: center;
    font-weight: bold;
    margin-top: 40px;
    font-size: 24px;
}
.backBtn{
    background-color: #9d98ff;
    border-radius: 30px;
    border: 0;
    outline: 0;
    width: 200px;
    height: 45px;
    color: #fff;
    font-size: 16px;
    margin-top: 20px;
    cursor: pointer;
}
.backBtn:hover{
    background-color: #b1acff;
}
.payment-info div{
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-right: 1px solid #d2d2d2;
    padding-right: 20px;
}
.pay-btn{
    float: left;
    width: 300px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    margin-top: 20px;
    background-color: #ff95da;
    border: 0;
    outline: 0;
    border-radius: 8px;
    color: #ffffff;
    font-size: 18px;
    transition: all .3s ease-in-out;
}
.pay-btn:hover{
    background-color: #ffbee8;
    color: #ff29b4;
}
.pay-btn p , .discount-btn p{
    margin: 0;
}
.discount-btn{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15px;
    width: 350px;
    height: 55px;
    font-size: 17px;
    border: 2px solid #ff95da;
    background: none;
    border-radius: 8px;
    transition: all .3s ease-in-out;
}
.discount-btn:hover{
    color: #ff95da;
}
.discount-btn i{
    font-size: 22px;
    color: #ff95da;
}