.wrapper{
    display: grid;
    grid-template-columns: 1fr 3fr;
}
.FilterWrapper{
    padding: 30px;
}
.title{
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid rgb(207, 207, 207);
    padding-bottom: 18px;
}
.title button{
    background-color: transparent;
    border: 0;
    outline: 0;
    color: #feace1;
    font-family: vazir, sans-serif;
    font-size: 14px;
    cursor: pointer;
}
.brand h4{
    font-weight: bold;
}
.brand div{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.brand p{
    font-size: 14px;
}
.checkBtn{
    height: 18px;
    width: 18px;
    border-radius: 5px;
    border: 1px solid rgb(181, 181, 181);
    outline: 0;
    background-color: transparent;
    cursor: pointer;
    position: relative;
}
.checkBtn:hover{
    background-color: #d7d7fb;
    border: 1px solid #cfcdff;
}
.checkBtn span{
    font-size: 18px;
    position: absolute;
    top: 0;
    left: 0;
    color: #827dff;
}
.filterBtn{
    margin-bottom: 20px;
    cursor: pointer;
    padding: 13px 20px;
    border: 0;
    outline: 0;
    background-color: #feace1;
    color: #fff;
    border-radius: 30px;
}
.filterBtn:hover{
    background-color: #ffc1e9;
}
.productwrapper{
    height: 100%;
    padding: 25px;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 25px;
}
.card{
    padding: 20px;
    background-color: #fff;
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    gap: 18px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.card img , .card p, .card h3{
    margin: 0;
}
.productImg{
    width: 50%;
    align-self: center;
}
.productPrice{
    color: #B1AFFF;
    font-size: 17px;
}
.addToCartBtn{
    background-color: #B1AFFF;
    border: 0;
    outline: 0;
    color: #fff;
    font-size: 16px;
    padding: 14px;
    cursor: pointer;
    border-radius: 30px;
    font-family: vazir , sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.4s ease-in-out;
}
.addToCartBtn:hover{
    background-color: #fff;
    border: 3px solid #B1AFFF;
    color: #B1AFFF;
}
.addToCartBtn p{
    margin-left: 8px;
}
.addToCartBtn span{
  font-variation-settings:
  'FILL' 0,
  'wght' 300,
  'GRAD' 0,
  'opsz' 48
}
.allBtn{
    background-color: transparent;
    cursor: pointer;
    border: 0;
    outline: 0;
    font-size: 15px;
}
.allBtn:hover{
    color: rgb(92, 92, 92);
}
.orderbyPrice{
    display: flex;
    background-color: #ffeffa;
    padding: 0px 30px;
    margin: 10px 40px;
    border-radius: 30px;
}
.orderbyPrice ul{
    display: flex;
    list-style-type: none;
    justify-content: center;
    align-items: center;
}
.orderbyPrice ul li{
    margin-left: 50px;
    cursor: pointer;
}
.orderbyPrice p{
    font-weight: bold;
}
.orderbyPrice li:hover{
    color: #ff43bd;
}
.searchBox {
    display: flex;
    align-items: center;
    margin-top: 30px;
    margin-bottom: 40px;
}
.searchBox input{
   width: 400px;
   padding: 15px;
   border-radius: 30px;
   border: 0;
   outline: 0;
   padding-right: 45px;
   font-size: 20px;
   margin-right: 40px;
   background-color: rgb(232, 232, 232);
   position: relative;
   font-family: vazir , sans-serif;
   font-size: 16px;
}
.searchBox input::placeholder{
   font-family: vazir , sans-serif;
   font-size: 16px;
}
.searchBox i{
   font-size: 30px;
   position: absolute;
   z-index: 1;
   right: 50px;
}

